export const getStoreNoList = ( obj ) => {
  return axios.request({
    url: '/api/Index/getStoreNo',
    params: obj,
    method: 'get'
  })
}

// 
export const uploadImg = (data) => {
  return axios.request({
    url: '/api/index/upload',
    data,
    method: 'POST',
  })
}

// 
export const getAllShopList = ( info ) => {
  return axios.request({
    url: '/api/StoreLease/index',
    params: info,
    method: 'get'
  })
}


