import axios from '@/js/api.request.js'


// 
export const getClueList = ( obj ) => {
  return axios.request({
    url: '/api/Clue/index',
    params: obj,
    method: 'get'
  })
}

// 
export const queryCustomer = ( data ) => {
  return axios.request({
    url: '/api/Clue/queryCustomer',
    data,
    method: 'post'
  })
}


